import { updateField } from 'vuex-map-fields'
import { findIndex } from 'lodash'
import { SchemaClient } from '@/models/SchemaClient.js'
import {
    RESET,
    SET_VALIDATED_STEP,
    REMOVE_STEP,
    RESET_FORM_DATA_CLIENT,
    UN_VALIDATED_STEP,
    UPDATE_FIELDS
} from './mutation-types'

import { initialState } from './state.js'

export const mutations = {
    updateField,
    [RESET](state) {
        Object.assign(state, initialState)
    },
    [RESET_FORM_DATA_CLIENT](state) {
        state.form_data_client = new SchemaClient()
    },
    [SET_VALIDATED_STEP](state, payload) {
        const { step, value } = payload
        const stepIndex = findIndex(state.form_validation_state, (o) => o.step === step)
        if (stepIndex >= 0) {
            state.form_validation_state[stepIndex].value = value
        }
    },
    [UN_VALIDATED_STEP](state, payload) {
        const arr = state.form_validation_state;
        let { step } = payload
        for (step; step < arr.length; step += 1) {
            arr[step].value = false
        }
    },
    [UPDATE_FIELDS](state, payload) {
        console.log(payload);
        state.form_data_client = { ...state.form_data_client, ...payload }
    },
    [REMOVE_STEP](state, payload) {
        const { step } = payload
        const stepIndex = findIndex(state.form_validation_state, (o) => o.step === step)
        console.log('removing step', stepIndex)
        if (stepIndex !== -1) { // returns -1, if not found
            state.form_validation_state.splice(stepIndex, 1)
        }
    },
    [RESET_FORM_DATA_CLIENT](state) {
        state.form_data_client = new SchemaClient()
    }
}
