<template>
    <fieldset class="form-masked-input__fieldset">
        <legend
            class="form-masked-input__legend"
            :class="{ 'error-font-color': validationError, 'success': isValid}"
            :style="{'background-color': bg_color}"
        >
            {{ legend }}
        </legend>
        <TheMask
            class="form-masked-input__input"
            :class="{ 'error-border': validationError, 'success': isValid}"
            :style="{'background-color': bg_color}"
            :value="field"
            :mask="mask"
            type="text"
            inputmode="numeric"
            pattern="[0-9]*"
            :placeholder="placeholder"
            :name="name"
            @input="onChange($event)"
            @blur="blur()"
        />
        <div
            v-if="validationError"
            class="form-masked-input__error"
            :style="{'background-color': bg_color}"
        >
            {{ validationError }}
        </div>
    </fieldset>
</template>

<script>
import { TheMask } from 'vue-the-mask';

export default {
    name: 'FormMaskedInput',
    components: {
        TheMask
    },
    props: {
        value: {
            type: String
        },
        legend: {
            type: String
        },
        type: {
            type: String
        },
        name: {
            type: String
        },
        field: {
            type: String
        },
        errors: {
            type: String
        },
        isValid: {
            type: Boolean
        },
        placeholder: {
            type: String
        },
        mask: {
            type: String
        },
        char: {
            type: String
        },
        bg_color: {
            type: String
        }
    },
    data() {
        return {
            val: ''
        };
    },
    computed: {
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    mounted() {
        // console.log(this.isValid);
    },
    methods: {
        onChange(event) {
            this.$emit('update:field', event);
        },
        emitEvent() {
            this.$emit('clicked');
        },
        blur() {
            // console.log(this.isValid);
        }
    }
};
</script>

