export class SchemaSupport {
    constructor({
        subject = '',
        chk_callback = false,
        chk_linked = false,
        linked_to_order = '',
        type = '',
        problem = '',
        resolution = '',
        message = ''
    } = {}) {
        this.subject = subject
        this.chk_callback = chk_callback
        this.chk_linked = chk_linked
        this.linked_to_order = linked_to_order
        this.type = type
        this.problem = problem
        this.resolution = resolution
        this.message = message
    }
}

export function createSupport(data) {
    return Object.freeze(new SchemaSupport(data));
}
