import { updateField } from 'vuex-map-fields';
import { SchemaPhone } from '@/models/SchemaPhone.js'

import { initialState } from './state.js'

import {
    RESET,
    START,
    SUCCESS,
    FAILURE,
    SET_DEFAULT
} from './mutation-types'

export const mutations = {
    updateField,
    [RESET](state) {
        Object.assign(state, initialState)
    },
    [START]: (state) => {
        state.started = true
    },
    [SUCCESS]: (state) => {
        state.verified = true
    },
    [FAILURE]: (state) => {
        state.verified = false
    },
    [SET_DEFAULT]: (state) => {
        state.verified = false
        state.started = false
        state.form_data = new SchemaPhone()
    }
}
