import Api from '@/helpers/api/index.js'

import {
    ACTION_CREATE_BATCH,
    ACTION_DELETE_ORDER,
    ACTION_NEW_ORDER,
    ACTION_EDIT_ORDER,
    ACTION_REPORT_PROBLEM
} from './action-types';

import {
    CLEAR_FORM,
    PUSH_TO_CART,
    REMOVE_FROM_CART,
    UPDATE_ORDER_IN_CART,
    UPDATE_CART_TOTAL,
    UPDATE_BATCH_WITH_BATCHID
} from './mutation-types';

export const actions = {
    [ACTION_CREATE_BATCH]({ state, rootState }, commit) {
        const {
            cart_items,
            cart_total,
            orders
        } = state;
        const {
            id,
            timezone
        } = rootState.client.client_data;

        orders.map((item) => {
            item.datetime = item.duedate;
            item.batchid = ''
            return item
        })

        const payload = {
            order: {
                order_form: {
                    batchid: '',
                    cart_items,
                    cart_total,
                    orders
                }
            },
            client: {
                client_form: {
                    clientid: id,
                    timezone
                }
            }
        }

        return new Promise((resolve, reject) => {
            const data = JSON.parse(JSON.stringify(payload))
            Api.post('/api/cp/order/create', data)
                .then((response) => {
                    commit(UPDATE_BATCH_WITH_BATCHID, {
                        batchid: response.data
                    })
                    resolve(response)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    },
    [ACTION_NEW_ORDER]({ commit, rootState }, payload) {
        return new Promise((resolve, reject) => {
            // check if total is a number
            if (Number.isNaN(payload.total)) { reject('No total provided') }
            // push order to cart
            try {
                commit(PUSH_TO_CART, payload.total)
            } catch (error) {
                reject(error)
            }
            // update total in cart
            try {
                commit(UPDATE_CART_TOTAL, rootState)
            } catch (error) {
                reject(error)
            }
            // const data = JSON.parse(JSON.stringify(rootState))
            // try {
            //     axios.post('/api/open/order/order-debug', data)
            //     .then(function (response){
            //         // do nothing
            //     })
            //     .catch(error => {
            //         reject(error)
            //     })
            // } catch (e) {

            // } finally {

            // }

            commit(CLEAR_FORM)
            resolve(true)
        })
    },
    [ACTION_EDIT_ORDER]({ commit, rootState }, payload) {
        return new Promise((resolve, reject) => {
            // check if total is a number
            if (Number.isNaN(payload.total)) { reject('No total provided') }
            if (Number.isNaN(payload.index)) { reject('No order index provided') }
            // update order to cart
            try {
                commit(UPDATE_ORDER_IN_CART, payload)
            } catch (error) {
                reject(error)
            }
            // update total in cart
            try {
                commit(UPDATE_CART_TOTAL, rootState)
            } catch (error) {
                reject(error)
            }
            commit(CLEAR_FORM)
            resolve(true)
        })
    },
    [ACTION_DELETE_ORDER]({ commit, rootState }, payload) {
        return new Promise((resolve, reject) => {
            if (Number.isNaN(payload.index)) { reject('No order index provided') }
            // update order to cart
            try {
                commit(REMOVE_FROM_CART, payload)
            } catch (error) {
                reject(error)
            }
            // update total in cart
            try {
                commit(UPDATE_CART_TOTAL, rootState)
            } catch (error) {
                reject(error)
            }
            resolve(true)
        })
    },
    [ACTION_REPORT_PROBLEM]() {
        return new Promise((resolve) => {
            resolve(true)
        })
    }
}
