import { getField } from 'vuex-map-fields'

export const getters = {
    getField,
    getOrderFormStatus(state) {
        let result = true
        state.form_validation_state.forEach((element) => {
            console.log(element.value)
            if (element.value === false) { result = false }
        })
        return result
    },
    getOrdersCount(state) {
        return state.orders.length
    },
    getOrdersInCart(state) {
        return state.orders
    },
    getCartTotal(state) {
        return state.cart_total
    }
}
