export class SchemaOrder {
    constructor({
        chk_linked = false,
        linked_to_order = '',
        type = '',
        academiclevel = '',
        duedate = '',
        pages = 2,
        slides = 0,
        charts = 0,
        problems = 0,
        sources = '',
        style = '',
        subject = '',
        title = '',
        details = '',
        chk_abstract = false,
        chk_outline = false,
        chk_faxes = false,
        chk_uploads = false,
        preferred_writers = [],
        blocked_writers = [],
        discount_code = '',
        discount_rate = '',
        discount_loyalty = false,
        total = 0

    } = {}) {
        this.chk_linked = chk_linked
        this.linked_to_order = linked_to_order
        this.type = type
        this.academiclevel = academiclevel
        this.duedate = duedate
        this.pages = pages
        this.slides = slides
        this.charts = charts
        this.problems = problems
        this.sources = sources
        this.style = style
        this.subject = subject
        this.title = title
        this.details = details
        this.chk_abstract = chk_abstract
        this.chk_outline = chk_outline
        this.chk_faxes = chk_faxes
        this.chk_uploads = chk_uploads
        this.preferred_writers = preferred_writers
        this.blocked_writers = blocked_writers
        this.discount_code = discount_code
        this.discount_rate = discount_rate
        this.discount_loyalty = discount_loyalty
        this.total = total
    }
}

export function createOrder(data) {
    return Object.freeze(new SchemaOrder(data));
}
