/* eslint-disable class-methods-use-this */

import moment from 'moment'

class Auth {
    constructor() {
        this.auth = {}
    }

    async login(data) {
        const clientData = {
            token: data.token,
            refresh_token: data.refresh_token,
            expires_in: data.expires_in,
            expires_date: data.expires_date
        }
        await window.localStorage.setItem('auth', JSON.stringify(clientData))
    }

    logout() {
        return new Promise((resolve) => {
            window.localStorage.removeItem('auth')
            resolve(true)
        })
    }

    logoutVuex() {
        return new Promise((resolve) => {
            window.localStorage.removeItem('vuex')
            resolve(true)
        })
    }

    isLogged() {
        if (!this.getToken() || this.getToken() === null) {
            this.logout()
            throw new Error('No token')
        }
        if (!this.isTokenValid()) {
            this.logout()
            throw new Error('Login session has expired')
        }
        return true
    }

    getLocalStorage() {
        const auth = window.localStorage.getItem('auth')
        if (!auth) return false
        return JSON.parse(auth)
    }

    getToken() {
        const { token } = this.getLocalStorage()
        return token
    }

    getTokenExpiry() {
        // returns a date string in UTC
        const { expires_date } = this.getLocalStorage()
        return expires_date
    }

    isTokenValid() {
        // !!!!  we go by UTC timezone !!!!!!
        // this means that the backend must return a date in ISO and UTC
        const token = moment.parseZone(this.getTokenExpiry()).valueOf()
        const now = moment().valueOf()
        return now < token
    }
}
export default new Auth()
