<template>
    <div
        v-if="dialog"
        class="confirm-modal"
        @click.self="close()"
    >
        <div class="confirm-modal__dialog">
            <header class="confirm-modal__header">
                <h2>{{ title }}</h2>
                <span
                    class="confirm-modal__close"
                    @click="close()"
                >
                    <CloseIcon />
                </span>
            </header>
            <section class="confirm-modal__content">
                <span>{{ message }}</span>
            </section>
            <footer>
                <div class="confirm-modal__two-button-block">
                    <button
                        class="confirm-modal__two-button confirm-modal__two-button--gray"
                        @click.prevent="close()"
                    >
                        cancel
                    </button>
                    <button
                        class="confirm-modal__two-button confirm-modal__two-button--main"
                        @click.prevent="submit()"
                    >
                        ok
                    </button>
                </div>
            </footer>
        </div>
    </div>
</template>

<script>
import CloseIcon from 'mdi-vue/Close.vue';

export default {
    name: 'ConfirmModal',
    components: {
        CloseIcon
    },
    data: () => ({
        dialog: false,
        resolve: null,
        reject: null,
        message: null,
        title: null
    }),
    methods: {
        open(title, message) {
            this.dialog = true
            this.title = title
            this.message = message
            return new Promise((resolve, reject) => {
                this.resolve = resolve
                this.reject = reject
            })
        },
        submit() {
            this.resolve(true)
            this.dialog = false
        },
        close() {
            this.resolve(false)
            this.dialog = false
        }
    }
}
</script>

