<template>
    <div
        class="form-datepicker"
        :class="{ 'error-border': validationError, 'success-border' : value}"
    >
        <fieldset class="form-datepicker__fieldset">
            <legend
                class="form-datepicker__legend"
                :class="{ 'error': validationError }"
            >
                {{ label }}
            </legend>
            <CalendarRange class="form-datepicker__icon" />
            <datetime
                type="datetime"
                format="DDDD, HH:mm"
                :flow="['date', 'time']"
                use12-hour
                :min-datetime="minDate"
                :minute-step="15"
                monday-first
                :value="field"
                placeholder="Select Date & Time"
                @input="onDateSelect($event)"
            />
            <div
                v-if="validationError"
                class="form-input__error"
            >
                {{ validationError }}
            </div>
        </fieldset>
    </div>
</template>

<script>
import { Datetime } from 'vue-datetime'
import 'vue-datetime/dist/vue-datetime.css'
import CalendarRange from 'mdi-vue/CalendarRange.vue'
import moment from 'moment'

export default {
    name: 'FormDatePicker',
    components: {
        datetime: Datetime,
        CalendarRange
    },
    props: {
        value: {
            type: String
        },
        label: {
            type: String,
            required: true
        },
        errors: {
            type: String
        },
        field: {
            type: String
        }
    },
    data: () => ({
        datetime: null
    }),
    computed: {
        minDate() {
            return moment().add(3, 'hours').toISOString();
        },
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    methods: {
        onDateSelect(event) {
            this.$emit('update:field', event)
        }
    }
};
</script>

