<template>
    <fieldset class="form-input__fieldset">
        <legend
            class="form-input__legend"
            :class="{ 'error-font-color': validationError }"
            :style="{
                'background-color': bg_color
            }"
        >
            {{ legend }}
        </legend>
        <component
            :is="prependIcon"
            class="form-input__prepend-icon"
        />
        <input
            ref="input"
            class="form-input__input"
            :style="{'background-color': bg_color}"
            :class="{ 'error-border': validationError, 'success': !validationError && value }"
            :type="type"
            :autofocus="autofocus"
            :name="name"
            :placeholder="placeholder"
            :value="field"
            :disabled="disabled"
            @input="onChange($event)"
            @blur="blur($event)"
        >
        <span @click="emitEvent">
            <component
                :is="appendIcon"
                class="form-input__append-icon"
            />
        </span>
        <!-- clearable -->
        <span v-if="clearable">
            <CloseIcon
                class="form-input__append-icon"
                @click="clearInput"
            />
        </span>
        <!-- clearable -->

        <div
            v-if="validationError"
            class="form-input__error"
            :style="{
                'background-color': bg_color
            }"
        >
            {{ validationError }}
        </div>
    </fieldset>
</template>

<script>
import EmailOutline from 'mdi-vue/EmailOutline.vue'
import Lock from 'mdi-vue/Lock.vue'
import Account from 'mdi-vue/Account.vue'
import AccountOutline from 'mdi-vue/AccountOutline.vue'
import Eye from 'mdi-vue/Eye.vue'
import EyeOff from 'mdi-vue/EyeOff.vue'

import Close from 'mdi-vue/Close.vue'

export default {
    name: 'FormInput',
    components: {
        EmailOutlineIcon: EmailOutline,
        LockIcon: Lock,
        AccountIcon: Account,
        EyeIcon: Eye,
        EyeOffIcon: EyeOff,
        CloseIcon: Close,
        AccountOutline
    },
    props: {
        value: {
            type: String
        },
        autofocus: {
            type: Boolean
        },
        disabled: {
            type: Boolean,
            default: false
        },
        prependIcon: {
            type: String
        },
        appendIcon: {
            type: String
        },
        legend: {
            type: String
        },
        type: {
            type: String
        },
        name: {
            type: String
        },
        field: {
            type: String
        },
        placeholder: {
            type: String
        },
        errors: {
            type: String
        },
        is_valid: {
            type: Boolean
        },
        bg_color: {
            type: String
        },
        clearable: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            val: ''
        };
    },
    computed: {
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    mounted() {
        // console.log(this.isValid);
    },
    methods: {
        onChange(event) {
            this.$emit('update:field', event.target.value);
        },
        emitEvent() {
            this.$emit('clicked');
        },
        blur(event) {
            this.$emit('blur', event.target.value)
        },
        clearInput() {
            this.value = ''
        }
    }
};
</script>

