            padding-left: 40px;
        }
    }
    &__append-icon {
        position: absolute;
        cursor: pointer;
        top: 50%;
        right: 12px;
        transform: translateY(-50%);
        color: rgba(0, 0, 0, 0.54);
    }

    &__fieldset {
        border: 1px solid #27d91f;
        border-radius: 4px;
        max-height: 50px;
        height: 50px;
        padding: 0;
        position: relative;
        width: 100%;
        &.error-border {

            .form-input__error {
                padding: 0 3px;
            }
        }
    }

    &__legend {
        display: table;
        color: #b3bbc6;
        caret-color: #b3bbc6;
        padding: 2px 5px 2px 5px;
        font-size: 80%;
        position: absolute;
        left: 6px;
        top: -8px;
        background-color: white;
        border: none;
    }

    &__input {
        box-sizing: border-box;
        display: inline-block;
        padding: 10px 40px 10px 10px;
        width: 100%;
        user-select: text;
        line-height: 46px;
        outline: none;
        border-radius: 4px;
        font-family: 'Mark Pro', sans-serif;
