import { getField } from 'vuex-map-fields'

export const getters = {
    getField,
    getCheckoutFormStatus(state) {
        let result = true
        state.form_validation_state.forEach((element) => {
            console.log(element.value)
            if (element.value === false) { result = false }
        })
        return result
    },
    getValidationState(state) {
        return state.form_validation_state
    }
}
