<template>
    <div
        class="checkox-block"
        :class="validationError && 'checkox-block-error'"
        @click="onClick"
    >
        <div class="checkox-container">
            <!-- <div class="checkbox-icon" v-if="!checked">
                <checkbox-icon />
            </div>
            <div v-if="checked" class="checkbox-icon">
                <checkbox-checked-icon />
            </div> -->
            <input
                :id="check"
                v-model="value"
                type="checkbox"
                class="form-checkbox"
                :checked="value"
                @click="onClick"
            >
            <label :for="check" />
        </div>
        <div
            v-if="label"
            class="checkox-block_label"
        >
            {{ label }}
        </div>
    </div>
</template>

<script>
// import Checkbox from 'mdi-vue/CheckboxBlankOutline.vue';
// import CheckboxChecked from 'mdi-vue/CheckBoxOutline.vue';

export default {
    name: 'FormCheckbox',
    components: {
        // CheckboxIcon: Checkbox,
        // CheckboxCheckedIcon: CheckboxChecked
    },
    props: {
        // eslint-disable-next-line vue/require-prop-types
        value: {},
        check: {
            type: String
        },
        label: {
            type: String
        },
        errors: {
            type: String
        }
    },
    data() {
        return {
            checked: false
        }
    },
    computed: {
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    created() {
        this.checked = this.value
    },
    methods: {
        onClick() {
            this.checked = !this.checked
            this.$emit('input', this.checked)
        }
    }
};
</script>

<style lang="scss" scoped>
