    height: 30px;
    width: 30px;
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
    // .checkbox-icon {
    //     span {
    //         display: inline-block;
    //         width: 100%;
    //         height: 100%;
    //     }
    // }
    // svg {
    //     color: rgba(0, 0, 0, 0.6);
    // }
    input {
        display: none;

        &:checked + label::before {
            opacity: 1;
        }
    }

    label {
        display: inline-block;
        position: relative;
        min-width: 30px;
        height: 30px;
        background: #FFFFFF;
        border: 1px solid #D3D9E2;
        box-sizing: border-box;
        border-radius: 4px;
        transition: 0.5s;
        cursor: pointer;

        &::before {
            content: '';
            position: absolute;
            background: url('~@/assets/img/svg/check.svg') no-repeat;
            width: 16px;
            height: 16px;
            left: 50%;
            margin-left: -8px;
            top: 50%;
            margin-top: -6px;
            opacity: 0;
            transition: 0.5s;
        }

    }
}
