import Api from '@/helpers/api/index.js'

import {
    GET_PRICING,
    GET_LIST_WRITERS,
    GET_REFERENCE_ORDERS,
    GET_LIST_NOTIFICATIONS
} from './action-types'

import {
    SET_PRICING
} from './mutation-types'

export const actions = {
    async [GET_PRICING]({ commit }, payload) {
        try {
            const response = await Api.get('/api/data/order', payload)
            commit(SET_PRICING, response.data)
            // resolve()
        } catch (error) {
            // reject()
        }
    },
    async [GET_LIST_WRITERS]() {
        const response = await Api.post('/api/writer/get-list')
        return response.data
    },
    async [GET_REFERENCE_ORDERS]() {
        const response = await Api.post('/api/cp/order/list-orders-reference-component')
        return response.data
    },
    async [GET_LIST_NOTIFICATIONS]() {
        const response = await Api.post('/api/cp/notification/list-notifications')
        return response.data
    }
};
