<template>
    <button
        class="btn-base btn-base_normal"
        :class="[disabledBtn, animatedBtn, defaultBtn, smallBtn, largeBtn, hoverBtn, classes]"
        :style="{width, height}"
        :type="type"
        @click="buttonClick"
    >
        <template v-if="loading">
            <button-loader />
        </template>
        <template v-else>
            <slot />
        </template>
    </button>
</template>

<script>
import ButtonLoader from '@/components/common/ButtonLoader.vue';

export default {
    name: 'CustomButton',
    components: {
        ButtonLoader
    },
    props: {
        loading: {
            type: Boolean
        },
        height: {
            type: String
        },
        width: {
            type: String
        },
        type: {
            type: String
        },
        classes: {
            type: String
        },
        disabled: Boolean,
        animated: Boolean,
        default: Boolean,
        bordered: Boolean,
        small: Boolean,
        large: Boolean,
        hover: Boolean
    },
    computed: {
        disabledBtn() {
            return this.disabled ? 'btn-base_normal btn-base_disabled' : ''
        },
        animatedBtn() {
            return this.animated ? 'btn-base_orange cart-btn-empty cart-btn-animated' : ''
        },
        defaultBtn() {
            return this.default ? 'btn-base_orange' : ''
        },
        smallBtn() {
            return this.small ? 'small-btn' : ''
        },
        largeBtn() {
            return this.large ? '' : ''
        },
        hoverBtn() {
            return this.hover ? 'hovered' : ''
        }
    },
    methods: {
        buttonClick() {
            this.$emit('on-btn-click')
        }
    }
}
</script>

<style lang="scss" scoped>
    .btn-base {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .btn-base.hovered {
        transition: .4s;
        &:hover {
            background: #ff7426 !important;
            border: 1px solid #ff7426;
            svg {
                color: #fff;
            }
        }
    }
    .small-btn {
        margin: 5px 5px 5px auto;
        background: transparent!important;
        border: 1px solid #e9e9e9;
        padding: 0;
        svg {
            width: 15px;
            color: #ff7426;
        }
    }
</style>
