<template>
    <div
        v-if="dialog"
        class="confirm-modal"
        @click.self="close()"
    >
        <div class="confirm-modal__dialog">
            <header class="confirm-modal__header">
                <h2>Report a Problem</h2>
                <span
                    class="confirm-modal__close"
                    @click="close()"
                ><CloseIcon /></span>
            </header>
            <section class="confirm-modal__content">
                <span v-if="auth">{{ client_data.firstname }}, have</span>
                <span v-else>Have </span>
                <span> you found a technical problem with our website? Please help us improve. Send it to our engineers now!</span>
            </section>
            <section class="confirm-modal__content">
                <form-textarea
                    v-model="$v.problem.$model"
                    legend="Detailed instructions"
                    type="text"
                    name="problem"
                    :field.sync="problem"
                    :errors="validationMessage($v.problem)"
                    :is-valid="$v.problem.$dirty && !$v.problem.$anyError"
                    @input.native="$v.problem.$touch();"
                    @blur.native="$v.problem.$touch();"
                />
                <form-input
                    v-if="!auth"
                    v-model="$v.email.$model"
                    legend="Email"
                    :field.sync="email"
                    name="email"
                    :is-valid="$v.email.$dirty && !$v.email.$anyError"
                    :errors="validationMessage($v.email)"
                    @input.native="$v.email.$touch()"
                    @blur.native="$v.email.$touch()"
                />
                <div
                    v-if="!auth"
                    class="confirm-modal__phone"
                >
                    <form-input
                        legend="Phone Number"
                        type="tel"
                        name="phone_number"
                        :field.sync="phone_number"
                        :errors="validationMessage($v.phone_number)"
                        :is-valid="$v.phone_number.$dirty && !$v.phone_number.$anyError"
                        @input.native="$v.phone_number.$touch()"
                        @blur.native="$v.phone_number.$touch()"
                    />
                    <span
                        class="confirm-modal__delete"
                        @click="clearPhone()"
                    ><CloseIcon /></span>
                </div>
            </section>
            <footer>
                <div class="confirm-modal__one-button-block">
                    <!--                    <button class="confirm-modal__button confirm-modal__button&#45;&#45;gray" @click="close()">cancel</button>-->
                    <button
                        class="confirm-modal__one-button confirm-modal__one-button--main"
                        outlined
                        @click="prevalidate()"
                    >
                        ok
                    </button>
                </div>
            </footer>
        </div>
    </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'

import { validationRules } from '@/validation/problem/RulesReportAProblem'
import { formMessages } from '@/validation/problem/MessagesReportAProblem'

import CloseIcon from 'mdi-vue/Close.vue';
import { mapState } from 'vuex'

import { mapClientFormFields } from '@/store/modules/checkout/'
import { mapPhoneFields } from '@/store/modules/phone'

import Api from '@/helpers/api/index.js'
import { eventBus } from '@/helpers/event-bus/'

export default {
    name: 'ReportModal',
    components: {
        CloseIcon
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data() {
        return {
            comment: '',
            loadingSubmit: false,
            dialog: false,
            problem: ''
            // email: null,
            // phone_number: null
        }
    },
    computed: {
        ...mapState('client', {
            client_data: 'client_data',
            auth: 'auth'
        }),
        ...mapClientFormFields([
            'form_data_client.email'
        ]),
        ...mapPhoneFields([
            'form_data.phone_number'
        ])
    },
    mounted() {
        if (this.auth) {
            this.email = this.client_data.email
            this.phone_number = this.client_data.phone
        }
    },
    methods: {
        open(options) {
            this.dialog = true
            this.title = options.title
            this.description = options.description
            return new Promise((resolve, reject) => {
                this.resolve = resolve
                this.reject = reject
            })
        },
        close() {
            this.resolve(false)
            this.dialog = false
        },
        clearPhone() {
            this.phone_number = ''
        },
        prevalidate() {
            this.validate()
        },
        validationMessage: validationMessage(formMessages),
        async submit() {
            const vm = this
            vm.loadingSubmit = true
            Api.post('/api/open/order/report-problem', {
                email: vm.email,
                phone: vm.phone_number,
                details: vm.problem
            }).then(() => {
                eventBus.$emit('showSnackBar', 'Your comment has been saved', 'success')
                vm.dialog = false
                vm.email = ''
                vm.phone_number = ''
                vm.problem = ''
            })
            // try {
            //     // const response = await vm.actionFileApplicantComment(vm.$data)
            //     // vm.$emit('submit', response)
            //     // eventBus.$emit('showSnackBar', `Your comment has been saved`, 'success');
            // } catch (e) {
            //     // eventBus.$emit('showSnackBar', e.data.message, 'error');
            // } finally {
            //     vm.loadingSubmit = false
            // }
        }
    }
}
</script>

