<template>
    <div class="form-radio">
        <label class="form-radio_label">
            <input
                type="radio"
                :name="name"
                :value="data"
                :checked="isChecked"
                class="form-radio_input"
                @input="$emit('input', $event.target.value)"
            >
            <span class="form-radio_circle" />
            {{ text }}
        </label>
    </div>
</template>

<script>
export default {
    name: 'FormRadio',
    props: {
        text: {
            type: String,
            required: true
        },
        name: {
            type: String,
            required: true
        },
        data: {
            type: String,
            required: true
        },
        isChecked: {
            type: Boolean,
            defaul: false
        }
    }
}
</script>

