import { getField } from 'vuex-map-fields';

export const getters = {
    getField,
    isAuth(state) {
        return state.auth
    },
    isPhoneOnRecord(state) {
        if (!state.client_data.phone || !state.client_data.phone_code) {
            return false
        }
        return true
    },
    isAddressOnRecord(state) {
        if (!state.client_data.address) return false
        if (!state.client_data.city) return false
        if (!state.client_data.country) return false
        return true
    },
    isClientPhoneVerified(state) {
        console.log(state.client_data.phone_verified);
        return Boolean(state.client_data.phone_verified)
    },
    getDataFields(state) {
        return {
            address: state.client_data.address,
            city: state.client_data.city,
            zip: state.client_data.zip,
            country: state.client_data.country,
            region: state.client_data.region
        }
    }
}
