const OrderForm = () => import(/* webpackChunkName: "order-form" */ '@/views/order/Main.vue')
const Checkout = () => import(/* webpackChunkName: "checkout" */'@/views/checkout/Checkout.vue')
const SuccessfulPayment = () => import(/* webpackChunkName: "successful-payment" */'@/views/checkout/SuccessfulPayment')

export default [
    {
        // this creates new order and puts it in the cart
        path: '/order/new',
        component: OrderForm,
        name: 'order_new'
    },
    {
        // this edits existing order
        path: '/order/edit/:index',
        component: OrderForm,
        name: 'order_edit'
    },
    {
        // checkout page
        path: '/checkout',
        component: Checkout,
        name: 'checkout'
    },
    {
        path: '/checkout/success',
        component: SuccessfulPayment,
        name: 'checkout_success'
    }
]
