<template>
    <div
        v-if="showPopup && flag"
        class="popup"
    >
        <div class="popup-wrapper">
            <div class="popup-wrapper__image">
                <clock />
            </div>
            <div class="popup-wrapper__content">
                <p class="popup-wrapper__content-title">
                    Confirm the extension of the time on the site
                </p>
                <p class="popup-wrapper__content-subtitle">
                    Otherwise the session will be terminated
                </p>
                <custom-button
                    default
                    class="button btn-base_colored"
                    @on-btn-click="confirm"
                >
                    Confirm
                </custom-button>
            </div>
            <div
                class="popup-wrapper__cross"
                @click="closePopup"
            >
                <close />
            </div>
        </div>
    </div>
</template>

<script>
import Clock from 'mdi-vue/Clock'
import Close from 'mdi-vue/Close'

import { mapClientFields } from '@/store/modules/client'
import Api from '@/helpers/api';
import { eventBus } from '@/helpers/event-bus/'

export default {
    name: 'ConfirmAuthPopup',
    components: {
        Clock,
        Close
    },
    data() {
        return {
            showPopup: false,
            flag: true
        }
    },
    computed: {
        ...mapClientFields([
            'client_data',
            'client_data.refresh_token',
            'client_data.expires_date',
            'client_data.expires_in',
            'client_data.access_token',
            'client_data.token'
        ])
    },
    mounted() {
        eventBus.$on('showAuthPopup', () => {
            this.showPopup = true
        })
        eventBus.$on('resetPopupFlag', () => {
            this.flag = true
            this.showPopup = false
        })
    },
    methods: {
        async confirm() {
            try {
                const response = await Api.post('/api/auth/client/refresh-token',
                    { refresh_token: this.refresh_token })
                this.refresh_token = response.data.data.refresh_token
                this.expires_date = response.data.data.expires_date
                this.expires_in = response.data.data.expires_in
                this.access_token = response.data.data.access_token
                this.token = response.data.data.access_token
                this.flag = true
                this.showPopup = false
                // window.localStorage.setItem('client', JSON.stringify(this.client_data))
                // window.localStorage.setItem('expires_date', response.data.data.expires_date)
            } catch (e) {
                console.log(e)
            }
        },
        closePopup() {
            this.showPopup = false
            this.flag = false
        }
    }
}
</script>

