import {
    forEach,
    size,
    round
} from 'lodash'

import { updateField } from 'vuex-map-fields';
import { SchemaOrder } from '@/models/SchemaOrder.js'

import {
    RESET,
    CLEAR_FORM,
    SET_VALIDATED_STEP,
    RESET_CART,
    EDIT_ORDER,
    PUSH_TO_CART,
    UPDATE_ORDER_IN_CART,
    REMOVE_FROM_CART,
    UPDATE_CART_TOTAL,
    UPDATE_BATCH_WITH_BATCHID
} from './mutation-types'

import { initialState } from './state.js'

export const mutations = {
    updateField,
    [RESET](state) {
        Object.assign(state, initialState)
    },
    [SET_VALIDATED_STEP](state, payload) {
        const { step, value } = payload
        state.form_validation_state[step].value = value
    },
    [CLEAR_FORM](state) {
        console.log('CLEAR_FORM')
        state.form_data = new SchemaOrder()
        console.log('state.form_data after', state.form_data)
    },
    [RESET_CART](state) {
        Object.keys(initialState).forEach((key) => { state[key] = initialState[key] })
    },
    [EDIT_ORDER](state, payload) {
        console.log('editing order', payload)
        const currentOrder = state.orders[payload]
        state.form_data = JSON.parse(JSON.stringify(currentOrder))
    },
    [PUSH_TO_CART](state, payload) {
        const currentOrder = { ...state.form_data }
        currentOrder.total = payload
        state.orders.push(currentOrder)
    },
    [UPDATE_ORDER_IN_CART](state, payload) {
        const currentOrder = { ...state.form_data }
        currentOrder.total = payload.total
        const orderIndex = payload.index
        state.orders[orderIndex] = currentOrder
    },
    [UPDATE_CART_TOTAL](state, rootState) {
        const ordersObj = state.orders
        let cart_total = Number(0, 2)
        const cart_items = size(ordersObj)
        forEach(ordersObj, (item) => {
            if (item.total > 0) {
                cart_total += item.total
            }
        })
        const cart_total_bd = cart_total
        // get usable store credit amount
        let store_credit
        const data = rootState.client.client_data
        const account_balance = data.statistics ? data.statistics.account_balance : null

        if (account_balance >= cart_total) {
            store_credit = cart_total
            cart_total = 0
        } else {
            store_credit = account_balance
            cart_total -= account_balance
        }
        // update state
        state.store_credit = Number(store_credit)
        state.cart_total = Number(round(cart_total, 2))
        state.cart_total_bd = Number(round(cart_total_bd, 2))
        state.cart_items = cart_items
    },
    [REMOVE_FROM_CART](state, payload) {
        state.orders.splice(payload.index, 1)
    },
    [UPDATE_BATCH_WITH_BATCHID](state, payload) {
        state.batchid = payload.batchid
    }
}
