<template>
    <div class="progress-bg">
        <div
            v-show="field.length > 0"
            class="progress-list"
        >
            <span
                :class="{'red darken-2' : strength >= 0 && field.length < 7 || field}"
                class="progress"
            />
            <span
                :class="{'orange darken-2' : strength >= 50}"
                class="progress"
            />
            <span
                :class="{'green darken-2' : strength >= 75}"
                class="progress"
            />
        </div>
    </div>
</template>
<script>
export default {
    name: 'ProgressLinear',
    props: {
        validate: {
            type: Object
        }
    },
    data() {
        return {
            strength: 0,
            field: ''
        }
    },
    watch: {
        validate(val) {
            if (val) {
                this.strength = val.strength
                this.field = val.field
            } else {
                this.strength = 0
            }
        }
    }
}
</script>
