<template>
    <fieldset class="form-textarea__fieldset">
        <legend
            class="form-textarea__legend"
            :class="{ 'error-font-color': validationError }"
        >
            {{ legend }}
        </legend>
        <component
            :is="prependIcon"
            class="form-textarea__prepend-icon"
        />
        <textarea
            class="form-textarea__input"
            :class="{ 'error-border': validationError, 'success' : field.length >= 50 }"
            :type="type"
            :name="name"
            :value="field"
            :placeholder="placeholder"
            @keyup="onChange($event)"
        />
        <span @click="test">
            <component
                :is="appendIcon"
                class="form-textarea__append-icon"
            />
        </span>

        <div
            v-if="validationError"
            class="form-textarea__error"
        >
            {{ validationError }}
        </div>
        <div class="form-textarea__length">
            {{ field.length }}
        </div>
    </fieldset>
</template>

<script>
import EmailOutline from 'mdi-vue/EmailOutline.vue';
import Lock from 'mdi-vue/Lock.vue';
import Account from 'mdi-vue/Account.vue';
import Eye from 'mdi-vue/Eye.vue';
import EyeOff from 'mdi-vue/EyeOff.vue';

export default {
    name: 'FormTextarea',
    components: {
        EmailOutlineIcon: EmailOutline,
        LockIcon: Lock,
        AccountIcon: Account,
        EyeIcon: Eye,
        EyeOffIcon: EyeOff
    },
    props: {
        value: {
            type: String
        },
        prependIcon: {
            type: String
        },
        appendIcon: {
            type: String
        },
        legend: {
            type: String
        },
        type: {
            type: String
        },
        name: {
            type: String
        },
        field: {
            type: String
        },
        errors: {
            type: String
        },
        placeholder: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            val: ''
        };
    },
    computed: {
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    methods: {
        onChange(event) {
            this.$emit('update:field', event.target.value);
        },
        test() {
            this.$emit('clicked');
        }
    }
};
</script>

