    }
    &__legend {
        display: table;
        color: #b3bbc6;
        caret-color: #b3bbc6;
        padding: 2px 5px 2px 5px;
        font-size: 80%;
        position: absolute;
        left: 6px;
        top: -8px;
        background-color: white;
        border: none;
    }
    &__input {
        box-sizing: border-box;
        display: inline-block;
        padding: 10px 40px 10px 10px;
        width: 100%;
        font-size: 14px;
        user-select: text;
        line-height: 2em;
        border: none;
        outline: none;
        border-radius: 4px;
        font-family: 'Mark Pro', sans-serif;
