<template>
    <div
        v-click-outside="clickOutside"
        class="form-select"
        :class="{ 'error-border': validationError, 'success-border' : setValue, 'grey-border' : openListItem }"
        @click="openListItem = !openListItem"
    >
        <fieldset :class="openListItem ? 'form-select__fieldset active' : 'form-select__fieldset'">
            <legend
                class="form-select__legend"
                :class="{ 'error': validationError }"
            >
                {{ label }}
            </legend>
            <component
                :is="prependIcon"
                class="form-input__prepend-icon"
            />
            <input
                class="form-select__input"
                :value="setValue"
                type="text"
                :name="name"
                :placeholder="label"
                @focus="$event.target.select()"
                @input="onInputChanged($event)"
            >
            <ul
                v-if="openListItem"
                class="form-select__list"
            >
                <li
                    v-for="(item, index) in filteredList"
                    :key="index"
                    class="form-select__list-item"
                    @click="selectItem(item.orderid)"
                >
                    <p class="form-select__list-item-title">
                        &#171;{{ item.title }}&#187;
                    </p>
                    <p class="form-select__list-item-description">
                        {{ item.created_at_local | moment_from }} ago: {{ item.orderid }}, {{ item.status }}
                    </p>
                </li>
            </ul>
            <div
                v-if="validationError"
                class="form-select__error"
            >
                {{ validationError }}
            </div>
        </fieldset>
    </div>
</template>

<script>
import BookOpenVariant from 'mdi-vue/BookOpenVariant.vue';
import FileDocument from 'mdi-vue/FileDocument.vue';
import filtersMixin from '@/mixins/filtersMixin';
import ClickOutside from 'vue-click-outside';

export default {
    name: 'FormSelectProject',
    components: {
        BookOpenVariantIcon: BookOpenVariant,
        FileDocumentIcon: FileDocument
    },
    directives: {
        ClickOutside
    },
    mixins: [
        filtersMixin
    ],
    props: {
        value: {
            type: String
        },
        prependIcon: {
            type: String
        },
        items: {
            type: Array
        },
        label: {
            type: String
        },
        errors: {
            type: String
        },
        name: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            openListItem: false,
            filteredItems: [],
            search: ''
        };
    },
    computed: {
        setValue() {
            const isExist = this.items.some((el) => el.orderid === this.search);
            if (isExist) {
                const title = this.items.find((element) => element.orderid === this.search)
                return `${title.title} (ID: ${title.orderid})`;
            }
            return this.search;
        },
        filteredList() {
            if (this.search === this.value) {
                return this.items
            }
            return this.items.filter((item) => item.orderid.toLowerCase().includes(this.search.toLowerCase()));
        },
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    mounted() {
        this.search = this.value;
    },
    methods: {
        selectItem(value) {
            this.$emit('input', value);
            this.search = value
            this.openListItem = false;
        },
        onInputChanged(event) {
            this.openListItem = true;
            this.search = event.target.value;
            if (this.search === '') {
                this.$emit('input', '');
            }
        },
        setInitValue() {
            this.search = this.value;
        },
        clickOutside() {
            this.openListItem = false
        }
    }
};
</script>

