import Vue from 'vue'
import Router from 'vue-router'

import { routes } from '@/router/routesAll.js'

import { scroller } from 'vue-scrollto/src/scrollTo'
import Auth from '@/helpers/auth/'

import { eventBus } from '@/helpers/event-bus/'

Vue.use(Router);

const router = new Router({
    routes,
    mode: 'history',
    scrollBehavior(to, from, savedPosition) {
        if (to.name === from.name) return false
        // if need scroll to id
        if (to.hash) {
            setTimeout(() => {
                const element = document.getElementById(to.hash.replace(/#/, ''))
                if (element && element.scrollIntoView) {
                    const ScrollTo = scroller();
                    ScrollTo(element, {
                        duration: 600,
                        offset: -60
                    });
                    element.scrollIntoView({ block: 'start', behavior: 'smooth' })
                }
            }, 500)
        }
        if (savedPosition) {
            return savedPosition
        }
        return { x: 0, y: 0 }
    }
});

router.beforeEach((to, from, next) => {
    // set true for loading property so that we can display loader animation until a route is fully loaded
    router.app.loading = true

    if (to.matched.some((record) => record.meta.requiresAuth)) {
        try {
            Auth.isLogged()
            next()
        } catch (error) {
            Auth.logout()
            Auth.logoutVuex()
            eventBus.$emit('showSnackBar', error, 'error')
            router.push({ name: 'login', query: { redirect: to.path } })
            setTimeout(() => { window.location.reload() }, 2000)
        }
    } else {
        next();
    }
})

router.afterEach(() => {
    // remove loading animation, once our route is fully loaded
    router.app.loading = false
});

export default router
