<template>
    <div
        v-click-outside="clickOutside"
        class="form-select-writers"
    >
        <fieldset

            :class="[openListItem ? 'form-select-writers__fieldset active' : 'form-select-writers__fieldset', {'error-border': !styles}, {'success': selectedWriters.length > 0}]"
            @click.self="toggleList"
        >
            <legend class="form-select-writers__legend">
                {{ label }}
            </legend>
            <div class="form-select-writers__selected-block">
                <ul
                    class="form-select-writers__selected-list"
                    @click.self="toggleList"
                >
                    <li
                        v-for="(item, index) in value"
                        :key="index"
                        class="form-select-writers__selected-item"
                    >
                        <div
                            class="form-select-writers__avatar form-select-writers__avatar--small"
                            :class="{ 'error-border': !styles }"
                        >
                            <Avatar
                                :avatar="item.avatar"
                            />
                        </div>
                        <span>{{ item.sw_id }}</span>
                        <span
                            :class="[{ 'error': !styles }]"
                            class="form-select-writers__delete-btn"
                            @click="deleteWriter(index)"
                        >
                            <CloseCircle class="form-select-writers__btn-icon" />
                        </span>
                    </li>
                </ul>
                <span
                    v-if="!value.length"
                    class="form-select-writers__placeholder"
                    @click.self="toggleList"
                >
                    Select {{ label.toLowerCase() }}
                </span>
                <transition name="fade">
                    <ul
                        v-if="openListItem"
                        class="form-select-writers__list"
                    >
                        <li>
                            <div
                                class=""
                            >
                                <Magnify class="form-select-writers__icon form-select-writers__icon--prepend" />
                                <input
                                    ref="searchInput"
                                    v-model="search"
                                    class="form-select-writers__input"
                                    placeholder="Pretype writer's ID or nickname"
                                    type="text"
                                >
                                <span
                                    v-if="search.length > 0"
                                    @click.stop="clearSearchField()"
                                >
                                    <Close class="form-select-writers__icon form-select-writers__icon--append" />
                                </span>
                            </div>
                        </li>
                        <li
                            v-for="writer in filteredWriters"
                            :key="writer.sw_id"
                            @click="selectWriter(writer)"
                        >
                            <!-- <div class="form-select-writers__avatar" :style="{'background-image': writer.avatar}"></div> -->
                            <div
                                class="form-select-writers__list-item"
                                :class="{'form-select-writers__list-item--selected': isWriterSelected(writer)}"
                            >
                                <Avatar
                                    :avatar="writer.avatar"
                                    :nickname="writer.nickname"
                                    :firstname="writer.firstname"
                                    :lastname="writer.lastname"
                                    :email="writer.email"
                                />
                                <div class="form-select-writers__info">
                                    <span class="form-select-writers__info-title">{{ writer.nickname }}</span>
                                    <span class="form-select-writers__info-subtitle">ID{{ writer.sw_id }}, {{
                                        writer.statistics.orders_completed
                                    }} jobs, {{ writer.statistics.fb_rating }} score</span>
                                </div>
                            </div>
                        </li>
                    </ul>
                </transition>
            </div>
        </fieldset>
    </div>
</template>

<script>
import CloseCircle from 'mdi-vue/CloseCircle.vue';
import Avatar from '@/components/common/Avatar.vue';
import ClickOutside from 'vue-click-outside'
import Close from 'mdi-vue/Close.vue';
import Magnify from 'mdi-vue/Magnify.vue';

export default {
    name: 'FormSelectWriters',
    components: {
        CloseCircle,
        Avatar,
        Close,
        Magnify
    },
    directives: {
        ClickOutside
    },
    props: {
        writers: { type: Array },
        label: { type: String },
        value: { type: Array },
        // eslint-disable-next-line vue/require-prop-types
        styles: {},
        // eslint-disable-next-line vue/require-prop-types
        disabled_items: { Array },
        name: {
            required: true,
            type: String
        }
    },
    data() {
        return {
            openListItem: false,
            selectedWriters: [],
            search: ''
        };
    },
    computed: {
        filteredWriters() {
            const itemsWithoutDisabled = this.writers.filter((item) => !this.disabled_items.some((el) => el.sw_id === item.sw_id));
            console.log('itemsWithoutDisabled', itemsWithoutDisabled)
            const filteredBySearch = itemsWithoutDisabled.filter((item) => item.sw_id.toLowerCase().includes(this.search.toLowerCase()) || item.nickname.toLowerCase().includes(this.search.toLowerCase()));
            console.log('filteredBySearch', filteredBySearch)
            return filteredBySearch;
        }
    },
    mounted() {
        this.initWriters()
    },
    methods: {
        selectWriter(writer) {
            // console.log(writer)
            const found = this.selectedWriters.find((element) => element.sw_id === writer.sw_id);
            // const foundPreferred = this.preferred_writers.find((element) => element.sw_id === value.sw_id);
            if (!found) {
                this.selectedWriters.push({ ...writer })
            } else {
                this.selectedWriters = this.selectedWriters.filter((element) => element.sw_id !== writer.sw_id)
            }

            // this.selectedWriters.push({ ...writer })
            this.$emit('input', [...this.selectedWriters]);
            // writer.active ? writer.active = false : writer.active = true
        },
        deleteWriter(index) {
            this.selectedWriters.splice(index, 1)
            this.$emit('input', [...this.selectedWriters]);
        },
        initWriters() {
            this.selectedWriters = [...this.value]
        },
        isWriterSelected(writer) {
            return this.value.some((el) => el.sw_id === writer.sw_id)
        },
        clickOutside() {
            this.openListItem = false
        },
        toggleList() {
            this.openListItem = !this.openListItem;
            if (this.openListItem) {
                this.focusOnSearchInput()
            }
        },
        focusOnSearchInput() {
            this.$nextTick(() => {
                this.$refs['searchInput'].focus();
            });
        },
        clearSearchField() {
            this.search = '';
        }
    }
}
</script>

