<template>
    <div id="app">
        <div class="page-wrapper">
            <router-view />
        </div>

        <!-- snack bar notification -->
        <snack-bar />
        <!-- snack bar notification -->

        <!-- confirm action, yes or no -->
        <confirm-modal ref="modal_confirm" />
        <!-- confirm action, yes or no -->
        <!-- report a problem modal -->
        <report-modal ref="modal_report_problem" />
        <!-- report a problem modal -->
        <!-- page overlay -->
        <overlay />
        <!-- auth popup -->
        <confirm-auth-popup />

        <span>
            HELLO HELLO HELLO
        </span>
        <!-- auth popup -->
    <!-- page overlay -->
    </div>
</template>
<script>
import SnackBar from '@/components/SnackBar.vue'
import Overlay from '@/components/modals/Overlay'
import ConfirmAuthPopup from '@/components/popups/ConfirmAuthPopup';
import { eventBus } from '@/helpers/event-bus/'

// modals components
import ConfirmModal from '@/components/modals/ConfirmModal';
import ReportModal from '@/components/modals/ReportModal'
import {
    ACTION_LOGOUT_CLIENT
} from '@/store/modules/client/action-types';
import { createNamespacedHelpers } from 'vuex';

const { mapActions: mapClientActions } = createNamespacedHelpers('client');

export default {
    components: {
        SnackBar,
        Overlay,
        ConfirmModal,
        ReportModal,
        ConfirmAuthPopup
    },
    data() {
        return {
            showUpdateUI: true
        }
    },
    created() {
        if (this.$workbox) {
            console.log(this.$workbox);
            this.$workbox.addEventListener('waiting', () => {
                this.showUpdateUI = true;
            });
        }
    },
    mounted() {
        // initialize modals
        this.$root.$confirm = this.$refs.modal_confirm.open
        this.$root.$confirm_password = this.$refs.modal_report_problem.open
        // initialize intercom
        this.$intercom.boot();
        // offline listener
        window.addEventListener('offline', () => {
            const errorString = 'There is a problem with the internet connection, try refreshing the page please.'
            eventBus.$emit('showSnackBar', errorString, 'error')
        })
    },
    methods: {
        ...mapClientActions({
            actionLogoutClient: ACTION_LOGOUT_CLIENT
        }),
        async logout() {
            try {
                await this.actionLogoutClient()
                this.$router.push({ name: 'login' })
            } catch (e) {
                console.log(e)
            }
        },
        checkCurrentTime() {
        },
        async accept() {
            console.log(this.showUpdateUI);
            this.showUpdateUI = false;
            await this.$workbox.messageSW({ type: 'SKIP_WAITING' });
        }
    }
}
</script>
