import Api from '@/helpers/api/index.js';

import {
    ACTION_TICKET_SUBMIT,
    ACTION_TICKET_ASK_REPLY,
    ACTION_TICKET_SUBMIT_REPLY
} from './action-types'

import {
    SET_TICKETID
} from './mutation-types'

export const actions = {
    [ACTION_TICKET_SUBMIT]({ rootState }) {
        let data = {
            data_support: {
                message: rootState.support.form_data.message,
                subject: rootState.support.form_data.subject,
                linked_to_order: rootState.support.form_data.linked_to_order,
                chk_callback: rootState.support.form_data.chk_callback,
                type: rootState.support.form_data.type,
                problem: rootState.support.form_data.problem,
                resolution: rootState.support.form_data.resolution
            },
            data_client: {
                fullname: rootState.checkout.form_data_client.fullname,
                email: rootState.checkout.form_data_client.email
            },
            clientid: rootState.client.clientid
        }

        if (rootState.phone.form_data.country.phone_code) {
            data = {
                ...data,
                phone_code: `${rootState.phone.form_data.country.phone_code}`,
                phone: `${rootState.phone.form_data.phone_number}`
            }
        }

        return new Promise((resolve, reject) => {
            Api.post('/api/open/ticket-support/new-ticket', JSON.stringify(data))
                .then((response) => {
                    resolve(response)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    },
    [ACTION_TICKET_ASK_REPLY]({ commit }, payload) {
        return new Promise(() => {
            commit(SET_TICKETID, {
                id: payload.id
            })
        })
    },
    [ACTION_TICKET_SUBMIT_REPLY]({ rootState }) {
        return new Promise((resolve, reject) => {
            const tmp = {
                ticketid: rootState.support.form_data.ticketid,
                message: rootState.support.form_data.message
            }
            const dataInput = {
                data_support: { ...tmp }
            }
            const data = JSON.stringify(dataInput)
            Api.post('/api/cp/ticket-support/reply', data)
                .then(() => {
                    setTimeout(() => resolve(true), 2000)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    }
}
